body {
    background: lightgray;
    padding: 0;
    margin: 0;
    font-family: 'Courier New', Courier, monospace;
}

.kb-spacer {
    flex: 1 0 auto;
}

.kb-header {
    display: flex;
    width: 100%;
    height: 80px;
    background: #2d3943;
    padding: 10px;
    box-sizing: border-box;
    align-items: center;
}

.kb-header .kb-header-brand {
    display: flex;
    color: white;
}

.kb-header .kb-header-nav {
    display: flex;
    justify-content: right;
}

.kb-header .kb-header-nav >*{
    margin-left: 15px;
}


.kb-footer {
    display: flex;
    width: 100%;
    height: 200px;
    background: #414e58;
    color: white;
    font-size: 12px;
    align-items: center;
    justify-content: flex-end;
    padding: 10px;
    box-sizing: border-box;
}

.kb-input {
    padding: 8px;
    border-radius: 5px;
}

.kb-select {
    padding: 8px;
    border-radius: 5px;
}

.kb-root {
    padding: 20px 15px;
}

.kb-action-section {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

.kb-btn {
    border-radius: 5px;
    padding: 6px 12px;
    display: inline-block;
    border: 1px solid gray;
    font-size: 14px;
    line-height: 1.5;
    background-color: white;
    cursor: pointer;
}

.kb-btn:hover {
    background-color: #e0e0e0;
}

.kb-btn-link {
    padding: 6px 12px;
    display: inline-block;
    border: none;
    font-size: 14px;
    line-height: 1.5;
    cursor: pointer;
    background: transparent;
}

.kb-btn-link:hover {
    color: blue;
}